@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
:root {
    --dark: rgb(29, 33, 37);
    --dark-secondary: #334e68;
    --dark-text: #8c9095;
    --light-text: #f0f4f8;
    --dark-green: rgb(36, 158, 115);
    --light-green: rgb(133, 221, 183);
    --blue: rgb(17, 190, 221);
    --light-purple: #e1c7e0;
    --dark-purple: #c054be;
}

body {
    background-color: rgb(29, 33, 37);
    background-color: var(--dark);
    font-family: "Lato";
    margin: 0;
    overflow-x: hidden;
    overscroll-behavior-x: none;
}

.App-header {
    /* font-size: 20px; */
    color: #f0f4f8;
    color: var(--light-text);
    padding: 36px;
    padding-bottom: 12px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.App-select {
    padding: 8px;
    width: 20%;
}

.App-escape {
    padding: 8px;
    text-align: right;
    color: #8c9095;
    color: var(--dark-text);
}

.App-data {
    padding: 8px;
    text-align: left;
    color: #8c9095;
    color: var(--dark-text);
}

.App-step {
    color: white;
}

.App-step-text-center {
    text-align: center;
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

 ::-webkit-scrollbar-thumb {
    background: #fff1;
    border-radius: 4px;
}

 ::-webkit-scrollbar-track {
    background: none;
}
.Bubbles-lyricRow {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 12px;
}

.Bubbles-ogLyric,
.Bubbles-kbLyric {
    padding: 12px;
    margin-top: 4px;
    background-color: #456381;
    border-radius: 16px;
    color: var(--light-text);
}

.Bubbles-kbLyric {
    text-align: right;
}

.Bubbles-arrow {
    -webkit-align-self: center;
            align-self: center;
    color: var(--dark-text);
    min-width: 40px;
    text-align: center;
}

.Bubbles-layer {
    font-weight: 200;
    text-align: left;
    margin-top: 0;
    padding-left: 12px;
    color: var(--light-text);
    width: 300px;
}

b {
    font-weight: 200;
}

button {
    border: none;
    background-color: transparent;
    font-family: "Lato";
    color: var(--light-text);
    cursor: pointer;
}

.ogLyric-bad {
    background-color: var(--dark-purple);
    padding: 0px 4px;
    border-radius: 2px;
}

.alcohol .ogLyric-bad {
    background-color: #00875A;
}

.identity .ogLyric-bad {
    background-color: #0a8ba2;
}

.sexual .ogLyric-bad {
    background-color: #C054BE;
}

.profanity .ogLyric-bad {
    background-color: #d08b00;
}

.other .ogLyric-bad {
    background-color: #596066;
}

.violence .ogLyric-bad {
    background-color: #cb3d1d;
}

.kblyric-different {
    background-color: var(--light-text);
    padding-left: 4px;
    border-radius: 2px;
    color: var(--dark)
}

.kblyric-same.start {
    padding-left: 2px;
}
.IntroStep {
    -webkit-animation: fadeIn linear 2s;
            animation: fadeIn linear 2s;
    text-align: left;
    margin: 0 15vw;
    padding: 40vh 0;
}

.header {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 24px;
}

.desc {
    font-size: 24px;
}

.center-image {
    margin: auto;
    display: block;
    padding: 16px;
}

.Step2 {
    margin: 10vh 0;
}

.Step2-p {
    font-size: 1.5em;
    margin: 0 15vw 24px 15vh;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.page-container {
    color: var(--light-text);
    padding: 36px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
}

.title {
    text-align: left;
    margin-left: 5vw;
    margin-top: 0;
}

.timeline-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex: 1 1;
            flex: 1 1;
    min-height: 0;
}

.legend-container {
    padding: 0 24px;
}

.timeline-rect:hover {
    cursor: pointer;
}

.timeline-popup-box {
    border-radius: 15px;
    box-shadow: 0 0 40px -10px black;
}

.timeline-popup-button:hover {
    cursor: pointer;
}

.timeline-popup-flex {
    display: -webkit-flex;
    display: flex;
    margin: 0 24px 24px 24px;
    max-width: 100%;
    max-height: calc(100% - 3.2em - 56px);
}

.timeline-popup-header {
    text-align: center;
    color: black;
    margin-bottom: 12px;
}

.timeline-popup-title {
    position: -webkit-sticky;
    position: sticky;
    z-index: 12;
    margin: 0;
}

.timeline-popup-category {
    background-color: red;
    padding: 0 0 8px 0;
    margin: 0;
}

.timeline-popup-subtitle {
    background-color: lightgray;
    text-align: center;
    margin: 0;
    padding-bottom: 8px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 12;
}

.timeline-popup-left {
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    overflow: auto;
}

.timeline-popup-left-item {
    border-radius: 8px;
    margin: 8px;
    padding: 8px;
    box-shadow: 0 0 6px -1px rgb(130, 130, 130);
    background-color: white;
    transition: all 0.2s ease-in-out;
}

.timeline-popup-left-item:hover {
    color: white;
    background-color: black;
}

.timeline-popup-left-item-selected {
    background-color: orange;
}

.timeline-popup-left-item:hover {
    cursor: pointer;
}

.timeline-popup-right {
    overflow: auto;
    margin-left: 24px;
    width: 100%;
}

.timeline-popup-right-song-section {
    margin-bottom: 16px;
}

.timeline-popup-right-song-title {
    padding: 8px;
    font-weight: bold;
    width: calc(100% - 16px);
    background-color: gray;
    color: white;
    position: -webkit-sticky;
    position: sticky;
    top: 26px;
}

.timeline-popup-right-lyric-section {
    margin: 0 32px;
}

.timeline-popup-right-lyric-item {
    padding: 16px 0;
}

.timeline-popup-right-lyric-item:not(:last-child) {
    border-bottom: 1px solid gray;
}

#timeline-popup-button {
    background-position: center;
    background-color: black;
    background-image: url(/final-project-adultz-bopz-fp/static/media/close-button.450edde6.png);
    background-size: 20px 20px;
    box-shadow: 0 0 20px -1px black;
}
.year-scroller {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 20px;
    border-left: 2px solid var(--blue);
    max-height: 100%;
}

.single-year {
    font-size: 16px;
    padding: 4px;
    color: #797979;
    transition: color 200ms ease-in-out;
}

.single-year:hover,
.single-year.selected {
    color: #e0e0e0;
    cursor: pointer;
    font-size: 20px;
}
